#profile-icon-container {
  display: inline-block;
  background-image: var(--gradient);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  border-radius: 50%;
  width: 16rem;
  height: 16rem;

  canvas, #profile-icon-preview {
    width: 16rem;
    height: 16rem;
    clip-path: circle();
  }
}

#macbook-container {
  height: 52rem;
  position: relative;

  #macbook {
    position: absolute;
    top: 2rem;
    left: -13.2rem;
    right: -13.5rem;
    height: 110%;
    background-image: url('/static/img/macbook-light-1920.webp');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;

    @media (prefers-color-scheme: dark) {
      background-image: url('/static/img/macbook-dark-1920.webp');
    }

    @media (max-device-width: 500px) {
      background-image: url('/static/img/macbook-light-600.webp');
    }

    @media (max-device-width: 500px) and (prefers-color-scheme: dark) {
      background-image: url('/static/img/macbook-dark-600.webp');
    }

    @media (max-device-width: 900px) {
      background-image: url('/static/img/macbook-light-1000.webp');
    }

    @media (max-device-width: 900px) and (prefers-color-scheme: dark) {
      background-image: url('/static/img/macbook-dark-1000.webp');
    }

    @media (max-device-width: 1300px) {
      background-image: url('/static/img/macbook-light-1400.webp');
    }

    @media (max-device-width: 1300px) and (prefers-color-scheme: dark) {
      background-image: url('/static/img/macbook-dark-1400.webp');
    }
  }

  #macbook-code-container {
    position: absolute;
    top: 14.75rem;
    left: 11.9rem;
    right: 17.7rem;
    bottom: 4.65rem;
    overflow-y: hidden;
    overflow-x: hidden;

    #macbook-code {
      height: 1250px;
      width: 100%;
      background-image: url('/static/img/code-light-686.webp');
      background-size: contain;
      background-position: top;
      background-repeat: no-repeat;

      @media (prefers-color-scheme: dark) {
        height: 1262px;
        background-image: url('/static/img/code-dark-686.webp');
      }
    }
  }
}

#cloud-tile {
  position: relative;

  .notification {
    box-shadow: 0 19px 38px rgba(0,0,0,0.05), 0 15px 12px rgba(0,0,0,0.02);
  }
}

#featured-project-description {
  z-index: 100;
}

#peerbridge-animation, #peerbridge-animation-preview {
  width: 100%;
  object-fit: contain;
  margin-top: -18rem;

  @media (max-device-width: 1023px) {
    margin-top: -6rem;
    margin-left: -80vw;
    width: 170vw;
    max-width: 170vw;
    overflow: hidden;
  }
}

#ml-presentation-container {
  position: relative;
  border-radius: 2rem;
  margin-top: 6rem;
  height: 60rem;

  #ml-presentation-front, #ml-presentation-gradient {
    border-radius: 2.5rem;
  }

  #ml-presentation-gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: var(--gradient);
    opacity: 1;
  }

  #ml-presentation-front {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: saturate(0.75);
  }

  #ml-presentation-text {
    position: absolute;
    border-radius: 2rem;
    padding: 2rem;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
    background-color: rgba(250, 250, 250, 0.9);

    @media (prefers-color-scheme: dark) {
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
}

#fullstack-image {
  position: relative;
  display: inline-block;
  height: 28rem;
  max-height: 100vw;
  width: 48rem;
  max-width: 100%;
  background-image: url('/static/img/tech-light-1000.webp');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 0 19px 38px rgba(0,0,0,0.05), 0 15px 12px rgba(0,0,0,0.02);
  border-radius: 24rem;

  @media (prefers-color-scheme: dark) {
    background-image: url('/static/img/tech-dark-1000.webp');
  }
}

#fullstack-image::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: var(--light);
  border-radius: 24rem;

  transform: scale(1);
  animation: light-pulse 4s infinite;

  @media (prefers-color-scheme: dark) {
    background: #1d1d1d;
    animation: dark-pulse 4s infinite;
  }

  @keyframes light-pulse {
    0% {
      box-shadow: 0 0 0 0 #0072ff, 0 0 0 0 #00c6ff;
    }

    50% {
      box-shadow: 0 0 0 8rem rgba(0, 0, 0, 0.01), 0 0 0 0.5rem rgba(0, 0, 0, 0);
    }

    100% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0), 0 0 0 0 rgba(0, 0, 0, 0);
    }
  }

  @keyframes dark-pulse {
    0% {
      box-shadow: 0 0 0 0 #0072ff, 0 0 0 0 #00c6ff;
    }

    50% {
      box-shadow: 0 0 0 8rem rgba(255, 255, 255, 0.01), 0 0 0 0.5rem rgba(255, 255, 255, 0);
    }

    100% {
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0), 0 0 0 0 rgba(255, 255, 255, 0);
    }
  }
}
